<template>
  <div class="container">
    <div class="avatar"><img :src="userInfo.headimgurl"></div>
    <div class="name">{{userInfo.nickname}}</div>
    <div class="title">绑定微信账号</div>
    <div class="btn-w">
      <mu-button @click="doBind" full-width color="success" data-mu-loading-size="24" data-mu-loading-color="#4caf50" v-loading="loading" :disabled="disabled">{{bindText}}</mu-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import wx from 'weixin-js-sdk'
export default {
  data () {
    return {
      host: 'http://leagoohealth.com:18080',
      userInfo: '',
      loading: false,
      disabled: false,
      code: '',
      username: '',
      bindText: '绑定授权',
    }
  },
  created () {
    if (window.location.href.split('?').length == 3) {
      this.code = window.location.href.split('?')[1].split('#')[0].split('&')[0].split('=')[1]
      this.username = window.location.href.split('?')[2].split('=')[1]
      let self = this
      axios({
        url: self.host + '/leagoo-health-manager/wechatuserinfo/getHeadImg',
        method: 'post',
        data: {
          username: self.username,
          code: self.code
        },
      }).then(response => {
        if (response.data.code == 200) {
          self.userInfo = response.data.data
        } else {
          alert(response.data.msg)
        }
      }, error => {
        console.log(error)
      })
    } else {
      window.location.replace('https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxbba4b3d497457d6b&redirect_uri=http%3a%2f%2fwww.leagoohealth.com%2fwxdd%2fpay%2f%23%2fbind?username=' + this.$route.query.username + '&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect')
    }
  },
  mounted () {

  },
  updated () {
      
  },
  methods: {
    doBind () {
      let self = this
      self.loading = true
      axios({
        url: self.host + '/leagoo-health-manager/wechatuserinfo/binding',
        method: 'post',
        data: {
          username: self.username,
          openid: self.userInfo.openid
        },
      }).then(response => {
        self.loading = false
        if (response.data.code == 200) {
          self.bindText = '绑定成功'
          self.disabled = true
        } else {
          self.bindText = '绑定失败'
        }
      }, error => {
        console.log(error)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.container{
  min-height: 100vh;
  overflow: hidden;
  background: url(../assets/bg.jpg) 0 bottom no-repeat;
  background-size: contain;
  background-color: #f1f3f2;
  .avatar{
    width: 80px;
    height: 80px;
    margin: 58px auto 10px;
    overflow: hidden;
    border-radius: 100%;
    img{
      display: block;
      height: auto;
      width: 100%;
    }
  }
  .name{
    text-align: center;
  }
  .title{
    font-size: 18px;
    text-align: center;
    padding: 20px 0;
    position: relative;
  }
  .btn-w{
    padding: 0 20px;
  }
}
</style>



