<template>
  <div class="container">
    <div v-html="payData" style="display: none;"></div>
    <mu-carousel :hide-controls="true" :hide-indicators="true" class="banner">
      <mu-carousel-item>
        <img src="../assets/banner1.jpg">
      </mu-carousel-item>
      <mu-carousel-item>
        <img src="../assets/banner2.jpg">
      </mu-carousel-item>
    </mu-carousel>
    <div class="list" v-if="orderInfo.productList.length > 0">
      <div class="item" v-for="item,index in orderInfo.productList">
        <img :src="item.img">
        <div class="explain">
          <p>{{item.name}}</p>
          <p style="margin: 10px 0;">规格：{{item.spec}}</p>
          <div class="price">¥{{item.dealSum}}</div>
          <!-- <p>生产厂家：{{item.supplyInfo}}</p> -->
          <!-- <p>适用症状：{{item.symptom}}</p> -->
        </div>
        <div class="price-num">
          <!-- <div class="price">¥{{item.dealSum}}</div> -->
          <div class="num">×{{item.count}}{{item.unit}}</div>
        </div>
      </div>
    </div>
    <div v-else class="no-data">{{noDataText}}</div>
    <div class="discounts">
      <div class="discounts_hd">优惠券：</div>
      <div class="discounts_bd">暂无可用优惠券</div>
      <div class="discounts_ft"><img class="icon" src="../assets/r.png"></div>
    </div>
    <div class="payment">
      <div class="info">
        <div>实付款：¥{{orderInfo.paySum}}</div>
        <div>优惠金额：¥{{orderInfo.reducedSum}}</div>
      </div>
      <mu-button @click="getData(true)" data-mu-loading-size="24" data-mu-loading-color="#4caf50" v-loading="loading">立即支付</mu-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import wx from 'weixin-js-sdk'
export default {
  //appid wxbba4b3d497457d6b 正式
  //appid wx2457d1288d0e6877 测试
  data () {
    return {
      discounts: '',
      orderId: '',
      host: 'http://leagoohealth.com:18081',//正式
      // host: 'http://test.leagoohealth.com:18081',//测试
      orderInfo: {
        productList: [],
        paySum: 0,
        reducedSum: 0,
      },
      noDataText: '',
      payData: '',
      loading: false,
    }
  },
  created () {
    if (/MicroMessenger/.test(window.navigator.userAgent)) {
      if (window.location.href.split('?').length == 3) {
        this.code = window.location.href.split('?')[1].split('#')[0].split('&')[0].split('=')[1]
        this.orderId = window.location.href.split('?')[2].split('=')[1]
        this.getData()
      } else {
        window.location.replace('https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + this.$route.query.appId + '&redirect_uri=http%3a%2f%2fwww.leagoohealth.com%2fpay%2f%23%2findex?orderId=' + this.$route.query.orderId + '&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect')
      }
    } else {
      this.orderId = this.$route.query.orderId
      this.getData()
    }
  },
  mounted () {
  
  },
  updated () {
      
  },
  methods: {
    toPay () {
      this.loading = true
      let self = this
      if (/MicroMessenger/.test(window.navigator.userAgent)) {
        axios({
          url: self.host + '/leagoo-health-api/api/pay/wxOrder',
          method: 'post',
          data: {
            orderId: self.orderId,
            code: self.code
          },
        }).then(response => {
          if (response.data.code == 200) {
            wx.config({
              debug: false, 
              appId: response.data.data.appId,
              timestamp: response.data.data.timeStamp,
              nonceStr: response.data.data.nonceStr,
              signature: 'MD5',
              jsApiList: [ "chooseWXPay", "closeWindow" ]
            })
            wx.ready(() => {
              wx.chooseWXPay({
                appId: response.data.data.appId,
                timestamp: response.data.data.timeStamp,
                nonceStr: response.data.data.nonceStr,
                package: response.data.data.package,
                signType: response.data.data.signType,
                paySign: response.data.data.paySign,
                success: function(res) {
                  self.loading = false
                  if (res.errMsg == "chooseWXPay:ok") {
                    self.$router.replace({ path:'/success' })
                  } else {
                    self.$router.replace({ path:'/error' })
                  }
                },
                cancel: function(res) {
                  self.loading = false
                  wx.closeWindow()
                }
              })
            })
          }
        }, error => {
          self.loading = false
          console.log(error)
        })
      } else if (/AlipayClient/.test(window.navigator.userAgent)) {
        axios({
          url: self.host + '/leagoo-health-api/api/pay/aliOrder',
          method: 'post',
          data: {
            orderId: self.orderId
          },
        }).then(response => {
          self.loading = false
          if (response.data.code == 200) {
            self.payData = response.data.form
            self.$nextTick(() => {
              document.forms[0].submit()
            })
          } else {

          }
        }, error => {
          console.log(error)
        })
      }
    },
    getData (pay) {
      let self = this
      axios({
        url: self.host + '/leagoo-health-api/api/order/orderDetail',
        method: 'post',
        data: {
          orderId: self.orderId
        },
      }).then(response => {
        if (response.data.code == 200) {
          self.orderInfo = response.data.data
          if (pay) {
            self.toPay()
          }
        } else {
          self.noDataText = '暂无订单信息'
        }
      }, error => {
        console.log(error)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.list{
  .item{
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    align-items: center;
    padding: 15px;
    background: #fff;
    border-bottom: 1px solid #eee;
    img{
      display: block;
      width: 80px;
      height: auto;
      
    }
    .explain{
      margin: 0 20px;
      flex: 1;
      color: #666;
      p{
        margin: 0;
        font-size: 15px;
      }
      .price{
        color: #fc5439;
        font-size: 16px;
      }
    }
    .price-num{
      text-align: center;
      color: #666;
      .price{
        color: red;
      }
    }
  }
}
.banner{
  height: 185px;
}
.payment{
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  height: 56px;
  background: #49b133;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 20px;
  .info{
    color: #fff;
    div:first-child{
      font-size: 18px;
    }
    div:last-child{
      font-size: 12px;
    }
  }
}
.container{
  margin-bottom: 56px;
}
.discounts{
  background: #fff;
  display: flex;
  justify-content: space-between;
  height: 52px;
  font-size: 14px;
  align-items: center;
  padding: 0 10px;
  color: #212121;
  .discounts_bd{
    flex: 1;
    text-align: right;
    color: #666;
  }
  .discounts_ft{
    .icon{
      display: block;
      width: 16px;
    }
  }
}
.no-data{
  background: #fff;
  line-height: 50px;
  text-align: center;
  color: #666;
}
</style>



